.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  list-style: none;
  margin: 0;
  padding: 0;
  grid-gap: 10px;
  justify-items: center;
}

.cardLink {
  display: block;
  text-decoration: none;
}

.cardLink:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
