.category {
  font-family: OpenSans, sans-serif;
  font-size: 16px;
  background-color: #fff;
  border: solid 1px #7e8fa4;
  border-radius: 25px;
  padding: 5px 15px;
  margin: 0 5px 5px 0;
  text-decoration: none;
  color: #323c48;
}

.category:hover,
.active {
  background-color: #445468;
  border-color: #445468;
  color: #fff;
}
